<template>
    <div>
        <basic-header :items="['用户管理','用户管理']"></basic-header>
        <el-card style="margin-top: 30px">
            <el-tabs v-model="activeName">
                <el-tab-pane label="用户查看" name="first">
                    <el-form  inline label-width="100px">
                        <el-form-item label="用户名称">
                            <el-input v-model="searchFormName"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="search" circle
                                       icon="el-icon-search circle"></el-button>
                        </el-form-item>
                    </el-form>
                    <el-table :data="tableData">
                        <el-table-column label="Id" prop="id"></el-table-column>
                        <el-table-column label="名称"  prop="nickName"></el-table-column>
                        <el-table-column label="头像" prop="avatarUrl"></el-table-column>
                        <el-table-column label="性别" prop="gender">
                            <template #default="o">
                                <span v-if="o.row.gender===1">男</span>
                                <span v-else="">女</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="地区" prop="area"></el-table-column>
                        <el-table-column label="open_id" prop="open_id"></el-table-column>
                        <el-table-column label="创建时间" prop="create_time"></el-table-column>
                        <el-table-column label="操作"></el-table-column>
                    </el-table>
                    <el-pagination
                            background
                            style="margin-top: 20px"
                            layout="prev, pager, next"
                            :total=total
                            :page-size.sync=pageSize
                            :current-page.sync=page
                    >
                    </el-pagination>
                </el-tab-pane>
                <el-tab-pane label="用户添加" name="second"></el-tab-pane>
            </el-tabs>
        </el-card>
    </div>
</template>

<script>
    import MBXhead from "../../components/MBXhead";
    export default {
        data() {
            return {
                activeName:'first',
                tableData:[],
                page:1,
                pageSize:5,
                total:0,
                searchFormName:'',
                api:"/api/user/user"
            }
        },
        methods: {
            fetchData(){
                let params={
                    page:this.page,
                    pageSize:this.pageSize
                }
                if(this.searchFormName){
                    params['nickName'] = this.searchFormName
                }
                this.axios.get(this.api,{params}).then(res=>{
                    if(res.data.code===200){
                        this.tableData=res.data.data;
                        this.total=res.data.total;
                    }
                })

            },
            search(){
                this.fetchData()
                this.$message.success("查询成功")
            }
            },
        created() {

        },
        watch:{
          page:function() {
              this.fetchData()
          }
        },
        components:{
            "basic-header":MBXhead
        },
        mounted() {
            this.fetchData()
        },
    }
</script>
<style scoped>

</style>